<template>
    <div>
        <headers :title="title"></headers>
        <div class="add-write">
            <ul>
                 <li>
                    <font class="fl">账户设置</font><i class="fr"></i>
                </li>
                 <li>
                    <font class="fl">密码管理</font><i class="fr"></i>
                </li>
                 <li>
                    <font class="fl">微信绑定</font><i class="fr"></i><span class="fr">未绑定</span>
                </li>
            </ul>
            <ul>
                 <li>
                    <font class="fl">收货地址管理</font><i class="fr"></i>
                </li>
                <li>
                    <font class="fl">关于孕娘管家</font><i class="fr"></i>
                </li>
                <li>
                    <font class="fl">投诉建议</font><i class="fr"></i>
                </li>
                 <li>
                    <font class="fl">清除缓存</font><i class="fr"></i><span class="fr">9.17M</span>
                </li>
            </ul>
             <ul @click="exit">
                 <li>
                    <font class="fl">退出登录</font><i class="fr"></i>
                </li>
            </ul>
        </div>
    </div>
</template>

<style lang="less" scoped>
  .add-write {
    font-family: "PingFang-SC-Regular";
    padding-top: 90px;
    width: 750px;
    ul {
      background: #fff;
      margin-top: 20px;
      padding-left: 32px;
      border-bottom: 1px solid #D4D4D6;
      li {
        font-size: 30px;
        color: #000;
        height: 98px;
        border-bottom: 1px solid #D4D4D6;
        font {
          font-family: "PingFang-SC-Regular";
          color: #000000;
          width: 212px;
          line-height: 98px;
        }
        span {
          font-size: 30px;
          color: #909197;
          line-height: 98px;
          padding-right: 17px;
        }

        i {
          width: 16px;
          height: 98px;
          background: url("../../assets/imgs/add-go.png") no-repeat left center;
          background-size: 100%;
          margin-right: 33px;
        }
      }
      li:last-child {
        border-bottom: 0;
      }
    }
  }
</style>

<script>
  import "lib-flexible"
  import { Dialog } from 'vant';
  import headers from '../../components/editHeader.vue'
  export default {
    components: {
      headers
    },
    data() {
      return {
        title: "设置"
      }
    },
    methods: {
      exit() {
        console.log(123);
        Dialog.confirm({
          title: '提示',
          message: '确认退出登录吗？'
        }).then(() => {
          // on confirm
        }).catch(() => {
          // on cancel
        });
      }
    },
    mounted() {
      document.body.style.backgroundColor = "rgb(247,246,247)";
    }
  }
</script>



